.page-catalog__title {
  font-family: "Oswald", serif;
  font-size: 34px;
  line-height: 60px;
  color: #111111;
  margin: 0;
  padding: 0;
  padding-left: 20px;
  padding-top: 15px;
  margin-top: 10px;
  padding-bottom: 28px;
  border-top: 1px solid #e6e6e6;
}

@media (min-width: @tablet-width) {
  .page-catalog__wrapper {
    margin: 0 auto;
    padding: 0 30px;
    width: 708px;
  }

  .page-catalog__title {
    font-family: "Oswald", serif;
    font-size: 60px;
    line-height: 60px;
    font-weight: 400;
    margin: 0;
    padding: 0;
    margin-top: 57px;
    margin-bottom: 26px;
    border: none;
  }
}

@media (min-width: @desktop-width) {
  .page-catalog__wrapper {
    margin: 0 auto;
    padding: 0 110px;
    width: 1220px;
  }

  .page-catalog__title {
    margin-top: 70px;
    margin-bottom: 25px;
    border: none;
  }
}
